@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;1,100&display=swap');
html,body{
  font-family: 'Raleway',sans-serif;
  padding:0;
  margin:0;
}
a{
  text-decoration: none;
  color:white;
  font-size: 20px;
}
.main{
  display:flex;
  justify-content: center;
  align-items: center;
  height:100vh;
  width:100vw;
  gap:25px;
  background: center / cover no-repeat url("../../assets/Background.png");
  &__form{
    display: flex;
    flex-direction: column;
  }
}